.container {
	position: relative;
	@include rmin(320) {
		width: 100%;
		padding:  0 15px; }
	@include rmin(768) {
		width: 750px;
		margin: 0 auto; }
	@include rmin(992) {
		width: 970px;
		padding:  0 20px; }
	@include rmin(1200) {
		width: 1000px;
		padding:  0 40px; } }
.col, .col-last {
	position: relative;
	flex-basis: 280px;
	flex-grow: 0;
	flex-shrink: 0;
	margin-right: 40px; }
.col-last {
	margin-right: 0; }
