$icons: (0:0);
$icons: map-merge($icons,('another-link-img': (X: -105px, Y:-158px, W: 64px, H: 64px, TW: 1101px, TH: 356px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('desc-image-2': (X: -179px, Y:-158px, W: 31px, H: 53px, TW: 1101px, TH: 356px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('desc-image': (X: 0px, Y:-158px, W: 95px, H: 103px, TW: 1101px, TH: 356px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('facebook': (X: -274px, Y:-158px, W: 10px, H: 19px, TW: 1101px, TH: 356px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('facebook@2x': (X: 0px, Y:-318px, W: 20px, H: 38px, TW: 1101px, TH: 356px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('phone': (X: -220px, Y:-158px, W: 44px, H: 36px, TW: 1101px, TH: 356px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('slider-btn': (X: 0px, Y:-271px, W: 34px, H: 37px, TW: 1101px, TH: 356px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('slider-line': (X: 0px, Y:0px, W: 1101px, H: 148px, TW: 1101px, TH: 356px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }

