.what-is-townhouse {
	padding: 80px 0 90px 0px;
	.container {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		flex-wrap: wrap;
		&:before {
			content: '';
			position: absolute;
			top: 35%;
			left: 0;
			@include s(slider-line);
			z-index: 0; } } }

.part-of-description {
	color: $simple-text-color;
	font: $simple-text-style;
	&:nth-of-type(1) {
		margin-right: 65px; } }
.what-is-townhouse__h2 {
	margin-bottom: 30px;
	margin-top: 5px; }
.what-is-townhouse__h2-mod {
	position: absolute;
	top: -110px; }
.what-is-townhouse__galery {
	position: absolute;
	right: -15px;
	top: 40%;
	color: #375272;
	font: 400 18px;
	font-family: $font-family;
	text-transform: uppercase;
	transform: rotate(-10deg);
	transition: all .3s;
	&:hover {
		text-decoration: underline; } }
