.advantages {
	background: url(../../img/advantages_bg.png);
	padding: 70px 0 30px 0;
	@include cover; }
.advantages__h2 {
	color: #fff;
	text-align: center; }
.advantages-image-block {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	margin-top: 45px; }
.advantages-image-block__item {
	flex-basis: 280px;
	flex-grow: 0;
	flex-shrink: 0;
	text-align: center;
	margin-bottom: 36px;
	a {
		display: inline-block;
		margin-bottom: 5px; }
	img {
		border: 2px solid #fff;
		border-radius: 50%;
		transition: all .3s;
		position: relative;
		&:hover {
			border: 10px solid #fff; } }
	span {}
	font: 600 20px $font-family;
	color: white; }
.advantages-description {
	.wrapper {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		p {
			color: #fff;
			font: $simple-text-style;
			margin-bottom: 30px; }
		p.mod {
			color: #aac8ea; } } }
.advantages-description__h2 {
	@extend .advantages__h2;
	margin-top: 30px;
	margin-bottom: 65px;
	color: #aac8ea; }
